body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    width: 100%;
    background-color: #282c34;
    color: white;
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: calc(10px + 2vmin);
    padding: 0 10%;
}

.App-link {
    color: #61dafb;
}

li {
    text-align: start;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.quiz_view_card {
    background-color: white;
    border-radius: 1rem;
    padding: 2rem;
}

.title {
    margin-right: 1rem;
}

.quiz_list {
    padding: 20px 10%;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    text-align: left;
}

.add_btn {
    width: 10rem;
    align-self: center;
}

.title {
    margin-right: 1rem;
}

.desc {
    font-size: 1.25rem;
}

.esc_button {
    height: 2.5rem;
    margin-left: 2rem;
}

.footer {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.score_report {
    font-size: 2rem;
    font-weight: 600;
}

.question {
    padding: 20px 0;
}

.answer_box {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.submit_btn {
    height: 2.5rem;
    margin-top: auto;
}

.sub_check {
    text-align: center;
}

.hidden {
    visibility: hidden;
}

.question_header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.edit_title_row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.edit_title_box {
    display: flex;
    align-items: center;
}

.edit_title_box > h4 {
    margin-bottom: 0.25rem;
}

.title_input {
    margin-left: 1rem;
    text-align: center;
}

.edit_question {
    padding: 20px 0;
}

.edit_answer_box {
    width: 50%;
}

.edit_question_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.center_section {
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
}

.points_input {
    width: 4rem;
    margin-right: 0.5rem;
}

.swap_button_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.swap_button {
    padding: 0.25rem;
    height: 2rem;
}

.swap_button + .swap_button {
    margin-top: 0.5rem;
}

.radio_question_box {
    display: flex;
    justify-content: left;
    align-items: center;
    grid-gap: 0.75rem;
    gap: 0.75rem;
}

.type_form {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    width: 50%;
}

.num_choices {
    width: 3.25rem;
}

.edit_footer {
    margin-top: 1rem;
    display: flex;
    justify-content: space-between;
}

.save_edit_btn {
    margin-right: 2rem;
}

.published_check {
    align-self: center;
}

.title_published_flex {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}
.add_question_button {
    display: block;
    margin: 0 auto;
}

.type_dropdown {
    min-width: 10rem;
}

.quiz_card {
    background-color: white;
    border-radius: 1rem;
    padding: 3rem;
}

.quizzer {
    background-color: cadetblue;
    padding: 30px 0;
}

.completedList {
    text-align: center;
    width: 60%;
    margin: 0 auto;
}

